  position: inherit;
  width: 100%;
  height:auto;
  left:0%;
  right:0%;
  margin-top:1%;
  margin-bottom: 360px;
} */
form {
  color:#AB9DF2;
  padding-top:20px;
}

label {
  margin-left: 10px
}

.button {
  margin-left: 10px;
}

a {
  color: #AB9DF2;
}

.grid {
  color: #AB9DF2;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  gap: 10px;
}

.campus-card {
  color:#AB9DF2
}
.defualt-icon {
  margin-top: 20px;
  width: 75px;
  height: auto
}

h3 {
  margin-top: 3px;
  margin-bottom: 10px;
  line-height: .5;
  text-decoration: underline;
}


* {
  font-size: 15px;
}

.navbar{
  position: fixed;
  display: inline-flex;
  width: 100%;
  margin-top: 0%;
  margin-bottom: 1px;
  left: 0%;
  right: 0%;
  top: 0;
  background-color: #AB9DF2;
}
ul {
  margin-top: 100px;
  list-style-type: none;
  margin: 0;
  width: 100%;
  display: flexbox;
  overflow: hidden;
  background-color:#AB9DF2;
}
.college-banner{
  position: inherit;
  width: 100%;
  height:auto;
  left:0%;
  right:0%;
}

li {

  float: left;
  width: 33%;
  justify-content: space-evenly;
  align-items: flex-start;
}
/*  border-style: solid;
  border-width: 10px;
  border-color: orange;*/

li a {
  display: block;
  color: black;
  text-align: center;
  padding: 16px;
  text-decoration: none;
  justify-content: space-evenly;
}

li a:hover {
  background-color: #111111;
  color: white;
}
